'use client';
import React, { useState, useEffect } from "react";
import api from "@/lib/axios";
import Sidebar from "@/components/sidebar";
import { useRouter } from "next/navigation";
import withAuth from "@/lib/withAuth";

// import withAuth from '../lib/withAuth';


// export default function Dashboard({isSidebarOpen}) {
  const Dashboard = () => {
  const [user, setUser] = useState(null);
    const router = useRouter();

    useEffect(() => {
        const fetchUser = async () => {
            try {
              const response = await api.get('/user', {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem('auth_token')}`, // Ensure token is sent
                },
              });
              setUser(response.data);
            }catch (error) {
              console.error("Error fetching user data:", error.response?.data || error.message);
              router.push('/'); // Redirect to login if user is not authenticated
            }
          };
      
          fetchUser();
        }, [router]);
            
            // try {
            //     const response = await api.get('/user');
            //     setUser(response.data);
            // } catch (error) {
            //   console.error(error.response?.data || error.message); 
            //     router.push('/');
            // }
     
   
    if (!user) return <p>Loading...</p>;
  return (
    <>
      <Sidebar data={user} />
      <aside id="dashboard"  className={`leftSide fixed top-5 right-4 z-40 w-4/5 bg-white cust_h-56 p-14 ${
          isSidebarOpen ? "w-4/5  translate-x-0" : " w-auto -translate-x-full" }`} aria-label="dashboard" >
            <h1 className="text-[#262A41] text-2xl">Dashboard</h1> 
        
      </aside>
    </>
  );
}
export default withAuth(Dashboard);
